<template>
  <div>
    <van-nav-bar title="会员中心" />
    <div class="head_img">
      <van-image
        round
        width="10rem"
        height="10rem"
        src="https://img.yzcdn.cn/vant/cat.jpeg"
      />
    </div>
    <div class="layout">
      <van-button type="danger" @click="layout">安全退出</van-button>
    </div>
    <van-cell-group>
      <van-cell title="我的收藏" is-link />
      <van-cell title="我的评论" is-link />
      <van-cell title="我的订单" is-link />
    </van-cell-group>
  </div>
</template>

<script>
import { Dialog } from "vant";
export default {
  methods: {
    layout() {
      this.$dialog.confirm({
        message: "确定要退出吗？",
      })
        .then(() => {
          this.$dialog({ message: "退出成功！" });
         sessionStorage.removeItem("user");
         this.$router.push("/login");
        })
        .catch(() => {
          this.$dialog({ message: "已取消退出！" });
        });
      
    },
  },
};
</script>

<style scoped>
.head_img {
  height: 200px;
  width: 100%;
  background: #f0f0f0;
  justify-content: center;
  display: flex;
  align-items: center;
}
.layout {
  padding: 20px 10px;
}
.van-button {
  width: 100%;
}
.van-cell-group {
  padding: 0 10px;
}
</style>


